import "core-js/modules/es.array.find.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/web.dom-collections.for-each.js";
// import
import { computed, defineComponent, ref } from 'vue';
import { useStore } from '@/store';
import { pageDialogConfig } from '@/views/main/system/user/config/pageDialogConfig';
import jxlstable from '@/baseui/tabel/src/table.vue';
import pageDialog from '@/components/page-dialog';
import { mapName, day } from '@/utils';
export default defineComponent({
  name: 'page-content',
  emits: ['handleRegester', 'handleVisibleChange', 'handleEdit'],
  components: {
    jxlstable: jxlstable,
    pageDialog: pageDialog
  },
  props: {
    pageContentConfig: {
      type: Object,
      required: true
    },
    listData: {
      type: Object,
      required: true
    },
    pageDialogConfig: {
      type: Object,
      default: function _default() {
        return {};
      }
    }
  },
  setup: function setup(props, content) {
    //<!-- ----------------------------------------------------- -->
    var store = useStore();
    var dialogtype = ref('');
    var pageData = ref({});
    var pageDialogRef = ref();
    var name = ref(mapName.page(props.pageContentConfig.pageName)); // 

    var page = ref(1); //  

    var pageSize = ref(10); //   listdata

    var itemCount = computed(function () {
      return props.listData.totalCount;
    }); // dialog

    var isShowDialog = ref(false); //<!-- ----------------------------------------------------- -->
    // 

    var handlePageSize = function handlePageSize(pagesize) {
      pageSize.value = pagesize;
      store.dispatch(name.value.queryAction, {
        //  0
        offset: (page.value - 1) * pageSize.value,
        size: pageSize.value
      });
    };

    var handlePage = function handlePage(pay) {
      page.value = pay;
      store.dispatch(name.value.queryAction, {
        //  0
        offset: (page.value - 1) * pageSize.value,
        size: pageSize.value
      });
    }; //<!-- ----------------------------------------------------- -->
    //  


    var handleRegester = function handleRegester() {
      if (pageDialogRef.value) {
        pageDialogRef.value.dialogData = {};
        pageDialogRef.value.isShowDialog = true;
      }

      dialogtype.value = 'regester';
      var passworditem = pageDialogConfig.formItems.find(function (pay) {
        return pay.field === 'password';
      });
      passworditem.isShow = true;
    }; //  


    var handleEdit = function handleEdit(item) {
      content.emit('handleEdit', item);

      if (pageDialogRef.value) {
        pageDialogRef.value.dialogData = item;
        pageDialogRef.value.isShowDialog = true;
      }

      dialogtype.value = 'patch';
      var passworditem = pageDialogConfig.formItems.find(function (pay) {
        return pay.field === 'password';
      });
      passworditem.isShow = false;
    }; //    pagedialog 


    var handleVisibleChange = function handleVisibleChange(item) {
      content.emit('handleVisibleChange', item);
    }; //  ~


    var handleRefresh = function handleRefresh() {
      store.dispatch(name.value.queryAction);
    }; //  


    var handleRemove = function handleRemove(pay) {
      store.dispatch(mapName.page(props.pageDialogConfig.pageName).deleteAction, pay);
    }; //  dialog form  ~


    var dialogResearch = function dialogResearch(pay) {
      var dialogData = ref({});
      var action = mapName.page(props.pageContentConfig.pageName); //

      props.pageDialogConfig.formItems.forEach(function (item) {
        if (item.field === 'createAt' || item.field === 'updateAt') {
          dialogData.value[item.field] = day.format(pay[item.field]);
        }

        dialogData.value[item.field] = pay[item.field];
      }); //   dialog

      if (dialogtype.value === 'regester') {
        store.dispatch(action.regesterAction, dialogData.value);
        pageDialogRef.value.isShowDialog = false; //    dialog
      } else if (dialogtype.value === 'patch') {
        //  dialogdata id  action  id url
        dialogData.value.id = pay.id;
        store.dispatch(action.patchAction, dialogData.value);
        pageDialogRef.value.isShowDialog = false;
      }
    }; // 
    // 1,  ..


    var pageContentSlots = mapName.slots(props.pageContentConfig.propList);
    var pageDialogSlots = mapName.slots(props.pageDialogConfig.formItems);
    return {
      mapName: mapName,
      page: page,
      pageSize: pageSize,
      itemCount: itemCount,
      isShowDialog: isShowDialog,
      pageData: pageData,
      dialogResearch: dialogResearch,
      pageDialogRef: pageDialogRef,
      pageContentSlots: pageContentSlots,
      pageDialogSlots: pageDialogSlots,
      handlePageSize: handlePageSize,
      handlePage: handlePage,
      handleRegester: handleRegester,
      handleVisibleChange: handleVisibleChange,
      handleRefresh: handleRefresh,
      handleEdit: handleEdit,
      handleRemove: handleRemove
    };
  }
});